.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 85%;
    height: 5rem;
    background-color: rgba(253,94,100,255);
    color: white;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
  }
  
  .title{
   font-size: small;
   margin-top: auto;
   margin-bottom: auto;
   color: #4d1601;
  }

  .main-image {
    width: 100%;
    height: 25rem;
    z-index: 0;
    overflow: hidden;
  }
  
  .main-image img {
    width: 110%;
    height: 100%;
    object-fit: cover;
    transform: rotateZ(-5deg) translateY(-3rem) translateX(-1rem);
  }